import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import * as dat from "dat.gui";
import * as TWEEN from "@tweenjs/tween.js";

import { LayerMaterial, Base, Depth, Fresnel, Noise } from "lamina/vanilla";
import { Vector3 } from "three";
import { CircleGeometry } from "three";

import { shader as vertexShader } from "./orb.vert";
import { shader as fragmentShader } from "./orb.frag";
import texUrl from "./tex1.png";
import tex2Url from "./b2.png";
import tex3Url from "./sprite1.png";



const loader = new THREE.TextureLoader();
const texture = loader.load(texUrl);
const texBlur = loader.load(tex2Url);
const texBlur2 = loader.load(tex3Url);

const sizes = {
  width: window.innerWidth,
  height: window.innerHeight,
};
const aspect = window.innerWidth / window.innerHeight;

const canvas = document.getElementById("canvas");
const scene = new THREE.Scene();
//scene.background = new THREE.Color("#ff0000")

const camera = new THREE.PerspectiveCamera(
  90,
  sizes.width / sizes.height,
  1,
  1000
);
camera.position.set(0, 0, 50);

// scene.add(camera);

const renderer = new THREE.WebGLRenderer({
  canvas: canvas,
  antialias: false,
  alpha: false,
});

renderer.setSize(window.innerWidth, window.innerHeight);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 1));
// const controls = new OrbitControls(camera, renderer.domElement);
// controls.enableDamping = true;

// orb

// create a bakgorund shader plane
const backgroundUniforms = {
  iTime: { type: "f", value: 100.0 },
  iResolution: { type: "v2", value: new THREE.Vector2() },
  iMouse: { type: "v2", value: new THREE.Vector2() },
  audio1: { type: "f", value: 1.0 },
  adj: { type: "f", value: 0.0 },
  orbOpacity: { type: "f", value: 1.0 },
  intensity: { type: "f", value: 1.0 },
  iChannel0: { type: "t", value: texture },
  surfaceDensity: { type: "f", value: 1.2 },
  mouseReal: { type: "v2", value: new THREE.Vector2() },
  mouseDown: { type: "v2", value: new THREE.Vector2() },
};
backgroundUniforms.iResolution.value.x = window.innerWidth;
backgroundUniforms.iResolution.value.y = window.innerHeight;

backgroundUniforms.adj.value = 0.2 - window.innerHeight / window.innerWidth;

const gui = new dat.GUI();

// gui.add(backgroundUniforms.audio1, "value", 0, 10);
//gui.add(backgroundUniforms.orbOpacity, "value", 0, 2);

const allParticles = [];
function particles() {
  var geometry = new THREE.BufferGeometry();
  var vertices = [];
  var materials = [], parameters;
  for ( var i = 0; i < 350; i ++ ) {
    var x = Math.random() * 60 - 30;
    var y = Math.random() * 60 - 30;
    var z = Math.random() * 60 - 30;
    vertices.push( x, y, z );
  }
  geometry.setAttribute( 'position', new THREE.Float32BufferAttribute( vertices, 3 ) );
  parameters = [
    [[ 0.3, 0.7, 0.9 ], texBlur, .2 ],
    [[ 0.3, 0.3, 0.8 ], texBlur2, .4 ]
  ];
  for ( var i = 0; i < parameters.length; i ++ ) {
    var color = parameters[ i ][ 0 ];
    var sprite = parameters[ i ][ 1 ];
    var size = parameters[ i ][ 2 ];
    materials[ i ] = new THREE.PointsMaterial( { size: size, map: sprite, blending: THREE.AdditiveBlending, depthTest: false, transparent: true, opacity:.35} );
    materials[ i ].color.setRGB( color[ 0 ], color[ 1 ], color[ 2 ] );
    var particles = new THREE.Points( geometry, materials[ i ] );
    particles.rotation.x = Math.random() * 6;
    particles.rotation.y = Math.random() * 6;
    particles.rotation.z = Math.random() * 6;
    allParticles.push(particles);
    scene.add( particles );
  }
}


// create custom shader material
let material = new THREE.ShaderMaterial({
  uniforms: backgroundUniforms,
  vertexShader: vertexShader,
  fragmentShader: fragmentShader,
});

// material = new THREE.MeshNormalMaterial();

// create object mesh, set size so it fills screen
// base this on resolution from above

var geometry = new THREE.PlaneGeometry(1, 1);
const backgroundPlane = new THREE.Mesh(geometry, material);
backgroundPlane.scale.set(110 * aspect, 110, 1);
scene.add(backgroundPlane);
particles();


var clock = new THREE.Clock();

let intensity = 1.0;


function animate() {
  requestAnimationFrame(animate);

  const t = clock.getElapsedTime();
  const d = clock.getDelta() * 100;

  //intensity += Math.random();
  //console.log(d);

  backgroundUniforms.iTime.value += 0.02;
  // console.log(backgroundUniforms.iTime.value)
  backgroundUniforms.audio1.value = 128.0 / 48.0 + Math.random() * 0.1;
  backgroundUniforms.iMouse.value.x =
    backgroundUniforms.iMouse.value.x +
    (mouse.x - backgroundUniforms.iMouse.value.x) * 0.05;
  backgroundUniforms.iMouse.value.y =
    backgroundUniforms.iMouse.value.y +
    (mouse.y - backgroundUniforms.iMouse.value.y) * 0.05;
  backgroundUniforms.mouseReal.value.x = mouseReal.x;
  backgroundUniforms.mouseReal.value.y = mouseReal.y;
  backgroundUniforms.mouseDown.value.x = downPos.x;
  backgroundUniforms.mouseDown.value.y = downPos.y;
  backgroundUniforms.intensity.value = intensity;

  if (!(vectorDistance > 0 || oTween?.isPlaying() || tween?.isPlaying())) {
    let newVal = backgroundUniforms.surfaceDensity.value + Math.sin(t * .5) * .01
    backgroundUniforms.surfaceDensity.value = Math.min(2, Math.max(0.7,newVal))
    // console.log(backgroundUniforms.surfaceDensity.value)
  }

  // Animate the particles
  for ( var i = 0; i < allParticles.length; i ++ ) {
    var object = allParticles[ i ];
    // console.log(allParticles)
      let rate = ( i < 1 ? 1 : -2);

      object.rotation.x = .01* backgroundUniforms.iTime.value * rate;
      object.rotation.z =  -.03* backgroundUniforms.iTime.value * rate;
  }

  camera.position.x += (-mouse.x * 0.01 - camera.position.x) * 0.05;
  camera.position.y += (mouse.y * 0.01 - camera.position.y) * 0.05;
  TWEEN.update();
  renderer.render(scene, camera);
}

let mouse = {
  x: 1,
  y: 1,
};
let mouseReal = {
  x: 1,
  y: 1,
};

let dragDistance = 0;
// Use vector distance to include x coordinates in distance
let vectorDistance = 0;
let mp1 = new THREE.Vector2(0, 0);
let mp2 = new THREE.Vector2(0, 0);
function mouseMove(e) {
  var x = e.clientX - window.innerWidth / 2;
  var y = e.clientY - window.innerHeight / 2;
  mouse.x = x;
  mouse.y = y;
  mouseReal.x = e.clientX;
  mouseReal.y = window.innerHeight - e.clientY;
  mp2.set(mouseReal.x, e.clientY);
  // drag handler
  if (downPos.y > 0) {
    dragDistance = (downPos.y - e.clientY) / window.innerHeight;
    vectorDistance = mp1.distanceTo(mp2) / window.innerHeight;
    //console.log(mp1, mp2);
    //console.log(dragDistance, vectorDistance);
    //console.log(dragDistance)

    const newVal = 1 + -vectorDistance * 0.9;

    backgroundUniforms.surfaceDensity.value = Math.max(0.3, newVal);
    console.log(backgroundUniforms.surfaceDensity.value);
  }

 
  

  //$(".background-video").css("transform","translate("+x+"px,"+y+"px)");
}

let oTween;
document.addEventListener("mousemove", mouseMove, { passive: true });

canvas.addEventListener("click", () => {
  console.log("fake click", vectorDistance);
  if (vectorDistance < 0.001) {
    console.log("real click", vectorDistance);

    oTween && oTween.stop();
    oTween = new TWEEN.Tween(backgroundUniforms.orbOpacity)
      .to({ value: 1 + THREE.Math.randFloat(-0.7, 1) }, 1000)
      .easing(TWEEN.Easing.Quadratic.Out)
      // .onUpdate((x) => {console.log(x,backgroundUniforms.orbOpacity)})
      .start();
  }
});

// function handlescroll(e) {
//   console.log(e);
// }

// canvas.addEventListener("mousewheel", handlescroll, false);

let downPos = {
  x: 0,
  y: 0,
};
let tween;
canvas.addEventListener(
  "mousedown",
  (e) => {
    downPos.y = e.clientY;
    downPos.x = e.clientX;
    mp1.set(downPos.x, downPos.y);
    // console.log(downPos);
    tween && tween.stop();
    oTween && oTween.stop();

    
    //  allParticles[0].material.color = new THREE.Color(1.0,0.0,0.0);
  },
  { passive: true }
);

canvas.addEventListener("mouseup", (e) => {
  downPos.y = 0;
  downPos.x = 0;
  tween && tween.stop();
  if (vectorDistance > 0) {
    tween = new TWEEN.Tween(backgroundUniforms.surfaceDensity)
      .to({ value: 1.1 }, 700)
      .easing(TWEEN.Easing.Quadratic.Out)
      // .onUpdate((x) => {console.log(x,backgroundUniforms.surfaceDensity)})
      .onComplete((y) => {
        vectorDistance = 0;
      })
      .start();
  }
}),
  { passive: true };

animate();
